                "DATA_TYPE": "Text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Product Code",
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "Product Name",
                "DATA_TYPE": "text",
                "SORTABLE": false,
