                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "Unique Name",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Unique Name",
                "DISPLAY_ORDER": 3
