                "COLUMN_NAME": "Location",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Location",
                "DISPLAY_ORDER": 4,

            },
            {
                "COLUMN_NAME": "Available Quantity",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Available Quantity",
                "DISPLAY_ORDER": 5,

            },
            {
                "COLUMN_NAME": "Purchase Value",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Purchase Value",
                "DISPLAY_ORDER": 6,

            },

            {
                "COLUMN_NAME": "Current book Value",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Current book Value",
                "DISPLAY_ORDER": 7,

            },
            {
                "COLUMN_NAME": "Salvage Cost",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Salvage Cost",
                "DISPLAY_ORDER": 8,
            },
            {
                "COLUMN_NAME": "Write-Off Value",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Write-Off Value",
                "DISPLAY_ORDER": 9,
            },

            ],
            LegendArray: [],
